body {
  margin: 0;
  font-family: sans-serif;
}

h1, h2, h3, h4, h5, h6, p, div, span {
  font-weight: 300;
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 50px white inset;
}

.flex-center {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

a:-webkit-any-link {
  color: inherit;
  text-decoration: none;
}

body.edition-mode .navigation {
  display: none;
}

.cards > div {
  margin: 8px;
}




